import React, { useContext, useEffect } from "react"
import { GlobalContext } from "../../../store/context"
import { Col, Button } from "react-bootstrap"
import BasketCard from "../../../components/cards/basket"
import { Link } from "gatsby"
import { CSSTransition, TransitionGroup } from "react-transition-group"
import "./style.scss"

const duration = 400

function isRender(FROM = [], BACK = []) {
  if (
    (FROM.length != 0 && FROM.filter(item => item.status == 1).length > 0) ||
    (BACK.length != 0 && BACK.filter(item => item.status == 1).length > 0)
  ) {
    return true
  } else return false
}
export default function BasketMobile() {
  const { BASKET = [], onRemoveBaksetItem = () => {} } = useContext(
    GlobalContext
  )
  const FROM = BASKET[0] || []
  const BACK = BASKET[1] || []

  return (
    <Col lg="3" className=" d-flex flex-column p-0">
      <div className="basketList">
        <TransitionGroup>
          {FROM.length &&
            FROM.map(({ place, offers, status, ticket_id }, index) => {
              if (status == 1) {
                return (
                  <CSSTransition
                    key={index + "basket_item"}
                    classNames="scale"
                    timeout={300}
                  >
                    <BasketCard
                      {...{ place, ...offers, status, ticket_id }}
                      callback={onRemoveBaksetItem(0)}
                    />
                  </CSSTransition>
                )
              }
            })}
        </TransitionGroup>
        <TransitionGroup>
          {BACK.length &&
            BACK.map(
              (
                {
                  place = null,
                  offers = null,
                  status = null,
                  ticket_id = null,
                },
                index
              ) => {
                if (status == 1) {
                  return (
                    <CSSTransition
                      key={index + "basket_item"}
                      classNames="scale"
                      timeout={300}
                    >
                      <BasketCard
                        {...{ place, ...offers, status, ticket_id }}
                        callback={onRemoveBaksetItem(1)}
                      />
                    </CSSTransition>
                  )
                }
              }
            )}
        </TransitionGroup>
      </div>

      {isRender(FROM, BACK) && (
        <CSSTransition classNames="scale" timeout={1000}>
          <div className="mt-auto mb-3 p-3">
            <Link to="/basket">
              <Button
                variant="danger"
                className="btn-block  zIndex bottom-0"
                style={{ bottom: 0, right: 0 }}
              >
                Бронировать
              </Button>
            </Link>
          </div>
        </CSSTransition>
      )}
    </Col>
  )
}
